import { useState, useEffect } from "react";
import {
  projectAuth,
  projectStorage,
  projectFirestore,
} from "../firebase/config";
import { useAuthContext } from "./useAuthContext";

export const useSignup = () => {
  const [isCancelled, setIsCancelled] = useState(false);
  const [error, setError] = useState(null);
  const [isPending, setIsPending] = useState(false);
  const { dispatch } = useAuthContext();

  const signup = async (email, password, displayName, thumbnail) => {
    setError(null);
    setIsPending(true);

    try {
      const res = await projectAuth.createUserWithEmailAndPassword(
        email,
        password
      );

      if (!res) {
        throw new Error("íšŒì›ê°€ì…ì„ ì™„ë£Œí•˜ì§€ ëª»í–ˆìŠµë‹ˆë‹¤");
      }

      // ì¸ë„¤ì¼ ì´ë¯¸ì§€ ì—…ë¡œë“œ ì„¬ë„¤ì¼sì— ìœ ì €ì˜ idë§ˆë‹¤ í´ë”ë¥¼ ë§Œë“¤ì–´ì£¼ê³  íŒŒì¼ì˜ ì´ë¦„ì„ í†µí•´ì„œ ì €ì¥í•´ì¤€ë‹¤.
      const uploadPath = `thumbnails/${res.user.uid}/${thumbnail.name}`;
      const img = await projectStorage.ref(uploadPath).put(thumbnail);
      const imgUrl = await img.ref.getDownloadURL();

      // ì‚¬ìš©ì ì´ë¦„ ì—…ë°ì´íŠ¸
      await res.user.updateProfile({ displayName, photoURL: imgUrl });

      // ìœ ì €ìš© document ë§Œë“¤ê¸° => ì»¬ë ‰ì…˜ì´ ì—†ë‹¤ë©´ í•˜ë‚˜ëŠ” ë§Œë“¤ì–´ì¤˜ì•¼ í•˜ë‚˜??
      await projectFirestore.collection("users").doc(res.user.uid).set({
        online: true,
        displayName: displayName,
        photoURL: imgUrl,
      });

      // ë¡œê·¸ì¸ ì•¡ì…˜ ë¿Œë ¤ì£¼ê¸°
      dispatch({ type: "LOGIN", payload: res.user });

      if (!isCancelled) {
        setIsPending(false);
        setError(null);
      }
    } catch (err) {
      if (!isCancelled) {
        setError(err.message);
        console.log(err.message);
        setIsPending(false);
      }
    }
  };

  useEffect(() => {
    return () => setIsCancelled(true);
  }, []);

  return { signup, error, isPending };
};
